}

.dropdown-toggle::after {
    display: none;
}

.nav-link {
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
    text-align: center;
    color: #444;
    transition: color .3s;
    padding: 0;
    position: relative;
}
.nav-link .icon-search {
    font-size: 15px;
}

.nav-link::before {
    content: '';
    display: block;
    position: absolute;
    bottom: -7px;
    opacity: 0;
    left: 0;
    z-index: 1;
    width: 10px;
    height: 2px;
    background-color: #e24545;
    transition: opacity .3s;
}
.dropdown.show .nav-link,
.active .nav-link,
.nav-link:hover {
    color: #e24545;
}

.active .nav-link {
    pointer-events: none;
}
.dropdown.show .nav-link::before,
.active .nav-link::before,
.nav-link:hover::before {
    opacity: 1;
}

.search-link::before {
    display: none;
}

.nav-item {
    padding: 0 10px;
}

.nav-item .icon-angle-down {
    margin-left: 8px;
}
.nav-item.show  .icon-angle-down {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.navbar-collapse {
    margin: 0;
}

.basket-holder {
    border: 2px solid #444;
