    color: #e24545;
}
.total-count {
    position: absolute;
    top: 50%;
    right: -16px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    border: 5px solid #fff;
    border-radius: 50%;
    background-color: rgb(34, 34, 34);
    width: 32px;
    height: 32px;
    z-index: 1;
    font-size: 11px;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.total-price {
