}
.navbar-toggler {
    height: 30px;
    outline: none!important;
}
.collapsed .burger::after,
.collapsed .burger:before,
.collapsed .burger {
    background-color: #444;
}
.burger {
    background-color: #e24545;
    position: relative;
    width: 25px;
    height: 3px;
    display: block;
}
.burger::after,
.burger:before {
    position: absolute;
    content: '';
    left: 0;
    right: 0;
    width: 100%;
    height: 3px;
    z-index: 2;
    background-color: #e24545;
}
.burger:before {
    top: -7px;
}
.burger::after{
    bottom: -7px;
}
.dropdown-link {
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 1.2;
    text-align: center;
    color: #444;
    padding: .25rem 1.5rem;
    display: block;
}
.dropdown-item {
    padding: 0;
}
.dropdown-item:focus, .dropdown-item:hover {
    background-color: transparent;
}
@media only screen and (max-width: 991px) {
    .navbar-collapse {
        flex-basis: auto;
        margin: 0;
        padding: 10px 0;
        position: absolute;
        top: calc(100% + 10px);
        left: 0;
        right: 0;
        background-color: #fff;
        border: 1px solid #eee;
        z-index: 10;
        opacity: 1;
        visibility: visible;
        -webkit-transform: translateY(0);
                transform: translateY(0);
        transition: opacity .3s, visibility .3s, -webkit-transform .3s;
        transition: transform .3s, opacity .3s, visibility .3s ;
        transition: transform .3s, opacity .3s, visibility .3s, -webkit-transform .3s;
    } 
    .collapse:not(.show) {
        display: block;
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
        opacity: 0;
        visibility: hidden;
        transition: opacity .3s, visibility .3s, -webkit-transform .3s;
        transition: transform .3s, opacity .3s, visibility .3s ;
        transition: transform .3s, opacity .3s, visibility .3s, -webkit-transform .3s;
    }
    .logo-holder {
        flex: 1 0;
    }
    .navbar {
        flex: 1 1 100%;
        order: 1;
        background-color: #fff;
        padding: 0;
    }

    .nav-item {
        margin-bottom: 10px;
        text-align: center;
    }    
    .nav-link::before {
        display: none;
    }
    .nav-link {
        display: inline-block;
    }  
    .dropdown {
        text-align: center;
    }
    .header {
        padding-top: 1rem;
        margin-bottom: 2rem;
    }
    .dropdown-menu{
         left: 50%;
         -webkit-transform: translateX(-50%);
                 transform: translateX(-50%);
         margin: 10px 0 0;
    }
}

