.img-thumbnail {
    border: none;
    border-radius: 0;
}

.card {
    border: 1px solid #eee;
    background-color: #fff;
    border-radius: 0;
    flex: 1 1;
    padding: 0;
    transition: background-color .3s;
}
.btn-thumbnail {
    position: absolute;
    z-index: 5;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 12px;
    color: #fff;
    text-transform: uppercase;
    border: 2px solid #fff;
    padding: 11px 20px;
    text-decoration: none;
    border-radius: 0;          
    opacity: 0; 
    transition: opacity .3s, color .3s, border .3s;
}
.btn-thumbnail:hover {
    color: #fff;
    border: 2px solid #e24545;
    text-decoration: none;
}
.img-thumbnail__holder {
    position: relative;
    width: 100%;
    padding-bottom: 96.15%;
    margin: 0 0 20px;
}

.img-thumbnail__holder img {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: auto;
    width: auto;
    max-height: 90%;
    max-width: 100%;
}

.img-thumbnail__holder::before {
    position: absolute;
    content: '';
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 1;
    width: 10px;
    height: 2px;
    background-color: #e24545;
}
.img-thumbnail__holder::after {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 4;
    opacity: 0;
    transition: opacity .3s;
}
.card:hover {
    background-color: #f6f6f6;
}
.card:hover .img-thumbnail__holder::after {
    opacity: 1;
}
.card:hover .btn-thumbnail {
    opacity: 1;
}
